.accordion-advantages {
    .accordion {
        &-card {
            padding: 30px 0;
            &-head {
                a {
                    color: #fecf73;
                    display: block;
                    width: 100%;
                    cursor: pointer;
                    text-transform: uppercase;
                    font-weight: 700;
                    position: relative;
                    padding-right: 140px;
                    padding-left: 40px;
                    i:first-child {
                        margin-right: 10px;
                        font-size: 30px;
                        position: absolute;
                        top: -3px;
                        left: 0; }
                    i:last-child {
                        margin-left: 15px;
                        position: absolute;
                        top: 10%;
                        right: 110px;
                        transform: rotate(0deg);
                        transition: all 0.3s ease; } }
                a.collapsed {
                    i:last-child {
                        position: absolute;
                        top: 10%;
                        right: 110px;
                        transform: rotate(180deg); } } }
            &-body {
                .table-1 {
                    thead {
                        white-space: nowrap;
                        background-color: #fecf73;
                        color: #ffffff;
                        tr th {
                            border: none;
                            padding: 25px 15px; } }
                    tbody {
                        tr td {
                            border-top: none;
                            border-bottom: 2px solid #fecf73;
                            padding: 25px 15px; } } }
                .table-2 {
                    thead {
                        background-color: #fecf73;
                        color: #ffffff;
                        tr th {
                            border: none;
                            padding: 25px 15px;
                            vertical-align: top; } }
                    tbody {
                        tr td {
                            border-top: none;
                            border-bottom: 2px solid #fecf73;
                            padding: 25px 15px; } } } } } } }

